import React from "react";
import logo from "../../assets/logo.svg";
import total from "../../assets/total.svg";
import orders from "../../assets/orders.svg";
import messages from "../../assets/messages.svg";
import phone from "../../assets/phone.svg";
import contragents from "../../assets/contragents.svg";
import docs from "../../assets/docs.svg";
import performers from "../../assets/performers.svg";
import reports from "../../assets/reports.svg";
import wiki from "../../assets/wiki.svg";
import settings from "../../assets/settings.svg";
import plus from "../../assets/plus.svg";
import exclamation from "../../assets/exclamation.svg";
import "./Menu.css";

function Menu() {
  const menuItems = [
    { img: total, title: "" },
    { img: orders, title: "" },
    { img: messages, title: "" },
    { img: phone, title: "", selected: true },
    { img: contragents, title: "" },
    { img: docs, title: "" },
    { img: performers, title: "" },
    { img: reports, title: "" },
    { img: wiki, title: " " },
    { img: settings, title: "" },
  ];

  return (
    <aside className="Menu">
      <img src={logo} alt="logo" className="Menu-logo" />
      {menuItems.map((item, index) => (
        <div
          key={index}
          className={`Menu-item ${item.selected && "Menu-item_selected"}`}
        >
          <div className="Menu-item_image">
            <img src={item.img} alt={item.title} />
          </div>
          <p>{item.title}</p>
        </div>
      ))}
      <button disabled={true} className="Menu-button">
        <p> </p>
        <img src={plus} alt=" " />
      </button>
      <button disabled={true} className="Menu-button">
        <p></p>
        <p></p>
        <img src={exclamation} alt="" />
      </button>
    </aside>
  );
}

export default Menu;
